import React from 'react'
import { makeStyles, Grid, Typography } from '@material-ui/core';
import { Carosel } from '../../../components/externo/Carosel';
import CardItem from '../../../components/CardItem';
import { CardInnovacion } from '../../../components/CardInnovacion';
import { useFetch } from '../../../hooks/useFetch';
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column'
  }

}));

export const Home = () => {

  const classes = useStyles();

  const { loading, data } = useFetch(`https://synergy-brack-end.herokuapp.com/proyectos`);


  return (
    <div className={classes.root}>
      <Carosel />
        <Typography variant="h3" align="center" gutterBottom>
          Soluciones para tus proyectos
        </Typography>
      <Grid container>
        {
          loading 
          ?
          (
              <div>
                  Loading...
              </div>
          ):(
            data.map((value,index) => {
              return(
                <Grid item  xs={6} sm={3} key={index}>
                  <CardItem imagen = {`https://synergy-brack-end.herokuapp.com${value.imagen[0].url}`} />
                 </Grid>
              )
            })
          )
        }
      </Grid>
      <Typography variant="h3" align="center" gutterBottom>
        Innovamos
      </Typography>
      <Grid container>
        <Grid item  xs={6} sm={3}>
          <CardInnovacion titulo='Calculador Cotizador' />
        </Grid>
        <Grid item  xs={6} sm={3}>
          <CardInnovacion titulo='Club del Instalador' />
        </Grid>
        <Grid item xs={6} sm={3}>
          <CardInnovacion titulo='Synergy Academy' />
        </Grid>
        <Grid item  xs={6} sm={3}>
          <CardInnovacion titulo='Bibloteca' />
        </Grid>
      </Grid>
    </div>
  )
};
