import React from 'react';
import { makeStyles, useScrollTrigger, Zoom, CssBaseline, Fab } from '@material-ui/core';
import { Cabecera } from '../../../components/Cabecera';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import { PiePagina } from '../../../components/PiePagina';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexDirection: 'column'
  },
  scroll: {
    position: 'fixed',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
  },
}));

const ScrollTop = ({ children, window }) => {
  const classes = useStyles();
  const trigger = useScrollTrigger({
    target: window ? window() : undefined,
    disableHysteresis: true,
    threshold: 100,
  });

  const handleClick = (event) => {
    const anchor = (event.target.ownerDocument || document).querySelector('#back-to-top-anchor');

    if (anchor) {
      anchor.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }
  };

  return (
    <Zoom in={trigger}>
      <div onClick={handleClick} role="presentation" className={classes.scroll}>
        {children}
      </div>
    </Zoom>
  );
};


const sections = [
  { title: 'Inicio', url: '/home' },
  { title: 'Productos', url: '/home/productos' },
  { title: 'Sistemas Especializados', url: '/home' },
  { title: 'Servicios', url: '/home' },
  { title: 'Club del Instalador', url: '/home' },
  { title: 'Contactos', url: '/home' },
];


export const Layout = ( { children, window } ) => {
  
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <CssBaseline />
      <Cabecera sections = {sections}/>
        <main>
            <div className={classes.appBarSpacer} />
             {children}
        </main>
      <ScrollTop {...{ children, window }}>
        <Fab color="primary" size="small">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
      <PiePagina />
    </div>
  );
}
