import React, { useState } from 'react'
import { makeStyles, 
         Grid, 
         List, 
         ListItem, 
         ListItemText, 
         Divider,
         Checkbox,
         Card,
         CardMedia,
         Typography,
         CardActionArea,
         TextField} from '@material-ui/core';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import SendIcon from '@material-ui/icons/Send';
import SearchIcon from "@material-ui/icons/Search";
import data from './Data';
import { useHistory } from "react-router-dom";
import Pagination from '@material-ui/lab/Pagination';


const useStyles = makeStyles((theme) => ({
    contendor: {
      display: 'flex',
      //margin: 2
    },
    paper: {
      padding: theme.spacing(5),
      textAlign: 'center',
     // color: theme.palette.text.secondary,
      margin:5
    },
    lista: {
      width: '100%',
      maxWidth: 290,
      backgroundColor: theme.palette.background.paper,
    },
    root: {
      padding: "2px 4px",
      display: "flex",
      alignItems: "center",
      width: 290,
      marginBlockStart:8,
      marginBlockEnd:8
    },
    icon: {
      marginRight: theme.spacing(2),
    },
    heroContent: {
      backgroundColor: theme.palette.background.paper,
      padding: theme.spacing(8, 0, 6),
    },
    heroButtons: {
      marginTop: theme.spacing(4),
    },
    cardGrid: {
     //  paddingTop: theme.spacing(2),
     //  paddingBottom: theme.spacing(2),
    },
    card: {
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
    },
    cardMedia: {
      paddingTop:350,
      
      // paddingTop: 500// '100%', // 16:9
    },
    cardContent: {
      flexGrow: 1,
    },
    
}));

export const Productos = () => {

  let history = useHistory();

  const classes = useStyles();

  const [selectedIndex, setSelectedIndex] = useState(0);
  const [checked, setChecked] = useState([]);
  const [categoria, setCategoria] = useState([]);
  const [portafolio, setPortafolio ] = useState([1, 2, 3, 4, 5, 6, 7, 8, 9]);

  const [titulo, setTitulo ] = useState('');

  const hanlderNav = () => {
    history.push('/home/contenido');
  }

  const handleListaClick = (event, index, item) => {
    setSelectedIndex(index);
    const { categoria, items } = item;
    setTitulo(categoria);
    setCategoria(items);
  };


  const handleToggle = (value, contenidos) => () => {

    const currentIndex = checked.indexOf(value);
    console.log(currentIndex);
    const newChecked = [checked];
    const { contenido } = contenidos;
    setPortafolio(contenido);

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }
    setChecked(newChecked);
 
  };

  return (
  <Grid container className={classes.contendor}>
      <Grid item xs={3}>
        <div className={classes.paper}>
        <Grid container spacing={1} alignItems="flex-end">
          <Grid item>
              <SearchIcon/>
          </Grid>
          <Grid item>
            <TextField id="input-with-icon-grid" label="Buscar" />
          </Grid>
        </Grid>
        <List component="nav">
              {
                data.map((value, index) => {
                  return (
                    <ListItem button
                              key={index}
                              selected={selectedIndex === index}
                              onClick={(event) => handleListaClick(event, index, value)}>
                    <ListItemIcon>
                      <SendIcon />
                    </ListItemIcon>
                    <ListItemText primary={value.categoria}/>
                  </ListItem>
                  )
                })
              }
            </List>
            <Divider />  
            <List>
                {categoria.map((value, index) => {
                  const labelId = `checkbox-list-label-${index}`;
                  return (
                    <ListItem key={index} role={undefined} dense button onClick={handleToggle(index, value)}>
                      <ListItemIcon>
                        <Checkbox
                          edge="start"
                          checked={checked.indexOf(index) !== -1}
                          tabIndex={-1}
                          disableRipple
                          inputProps={{ 'aria-labelledby': labelId }}
                        />
                      </ListItemIcon>
                      <ListItemText id={labelId} primary={value.titulo} />
                    
                    </ListItem>
                  );
                })}
              </List>
        </div>
      </Grid>
      <Grid item xs={9}>
        <div className={classes.paper}>
        <Typography variant="h5">
                {titulo}
        </Typography>
        <Grid container spacing={4}>
            {portafolio.map((card, index) => (
              <Grid item key={index} xs={12} sm={6} md={4}>
                <Card className={classes.card}>
                  <CardActionArea onClick = {hanlderNav}>
                  <CardMedia
                    className={classes.cardMedia}
                    image="https://source.unsplash.com/random"
                    title="Image title"
                  />
                  </CardActionArea>
                </Card>
              </Grid>
            ))}
            <Pagination count={10} variant="outlined" shape="rounded" />
          </Grid>
        </div>
      </Grid>
  </Grid>
  )
}