import React from 'react'
import { CssBaseline, AppBar, Toolbar, makeStyles, IconButton, Button } from '@material-ui/core'
import { useHistory } from "react-router-dom";

import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import YouTubeIcon from '@material-ui/icons/YouTube';


const useStyles = makeStyles((theme) => ({
    grow: {
      flexGrow: 1,
    },
    logotipo: {
      width: 200,
    },
    useButton: {
      height: 60,
      padding: '0 25px',
      "&:hover": {
        borderBottomWidth: 3, 
        borderColor: '#000000',
        borderStyle: 'solid'
    }
    },
    toolbarSecondary: {
      display: 'flex',
      justifyContent: 'center',
      overflowX: 'auto'
    },
    sectionDesktop: {
      display: 'flex',
    }
}));

export const Cabecera = ({sections}) => {

    let history = useHistory();

    const classes = useStyles();

    const hanlderNav = url => {
        history.push(url);
    }

    return (
        <div className={classes.grow}>
            <CssBaseline />
            <AppBar color="inherit">
                <Toolbar >
                  <img className={classes.logotipo}
                      src={require('../assets/Logo.png')}
                      alt='logotipo' />
                  <div className={classes.grow} />
                  <div className={classes.sectionDesktop}>
                      <IconButton>
                          <FacebookIcon />
                      </IconButton>
                      <IconButton>
                          <InstagramIcon />
                      </IconButton>
                      <IconButton>
                          <TwitterIcon />
                      </IconButton>
                      <IconButton>
                          <WhatsAppIcon />
                      </IconButton>
                      <IconButton>
                          <YouTubeIcon />
                      </IconButton>
                  </div>
                </Toolbar>
                <Toolbar component="nav" variant="dense" className={classes.toolbarSecondary}>
                {sections.map((section) => (
                    <Button key={section.title} 
                            className={classes.useButton}
                            onClick={() => hanlderNav(section.url)}>
                    {section.title}
                    </Button>
                ))}
                </Toolbar>
            </AppBar>
            <Toolbar id="back-to-top-anchor" />

        </div>
    )
}
