import React from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route
  } from "react-router-dom";
import { Bienvenido } from "../views/client/bienvenido/Bienvenido";
import { Layout } from "../views/client/layout/Layout";
import { Home } from "../views/client/home/Home";
import { Productos } from "../views/client/productos/Productos";
import { Servicios } from "../views/client/servicios/Servicios";
import { Contenido } from "../views/client/contenido/Contenido";
  
export default () => {
    return(
        <Router>
          <Switch>
            <Route path='/' 
                   component={Bienvenido} 
                   exact/>
            <Route path='/home/:path?'>
              <Layout>
                    <Switch>
                      <Route path='/home' exact component={Home} />
                      <Route path='/home/productos' component={Productos} />
                      <Route path='/home/contenido/:id' component={Contenido} />
                      <Route path='/home/Servicios' component={Servicios} />
                    </Switch>
              </Layout>
            </Route>
          </Switch>
      </Router>
    )
};